<template>
  <div class="header-bar">
    <h1 class="title">
      <router-link :to="{ name: 'Home'}">
        豆瓣
      </router-link>
    </h1>
    <ul class="nav">
      <li>
        <router-link tag="div" style="color: #2384E8" :to="{ name: 'MovieView'}">
          电影
        </router-link>
      </li>
      <li>
        <router-link tag="div" style="color: #9F7860" :to="{ name: 'BookView'}">
          图书
        </router-link>
      </li>
      <li>
        <router-link tag="div" style="color: #E4A813" :to="{ name: 'StatusView'}">
          广播
        </router-link>
      </li>
      <li>
        <router-link tag="div" style="color: #2AB8CC" :to="{ name: 'GroupView'}">
          小组
        </router-link>
      </li>
    </ul>
    <span class="talion" @click="toToast"></span>
  </div>

</template>

<script>

  //const url='https://api.douban.com/v2/movie/search?count=5&q='
  export default {
    name: "homeheader",
    data(){
      return {
      }
    },
    methods:{
      toToast(){
        this.$router.push({path:'ToastView'})
      }
    },

  }
</script>

<style scoped lang="less">
  .header-bar {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    z-index: 2;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
    align-items: center;
    height: 4.8rem;
    padding: 0 1.8rem;
    background: #fff;
    border-bottom: .1rem solid #f3f3f3;
    .title {
      flex: 1;
      max-width: 4.6rem;
      max-height: 2.2rem;
      margin-right: 1.8rem;
      font-size: 0;
      line-height: 3.2rem;
      color: #00b600;
      word-break: break-all;
      background: url(data:image/png;base64,) no-repeat;
      background-size: cover;

      a {
        display: block;
        text-decoration: none ;
      }
  }

    ul {
      display: flex;
      flex: 1;
      justify-content: flex-end;
    }

    li {
      display: inline-block;
      font-size: 1.6rem;
      margin-right: 1.2rem;
    }

    .talion {
      font-size: 0;
      background: url(data:image/png;base64,) no-repeat;
      background-size: cover;
      width: 2.4rem;
      height: 1.8rem;
      margin-top: 0.4rem;
    }
  }
</style>



