<template>
    <div class="container">
      <div class="list" v-for="list in data">
        <h3>{{list.data.title}}</h3>
          <div class="list-box">
            <template v-if="list.data.subjects">
              <div class="item" v-for="item in list.data.subjects"  >
                <img :src="item.images.small" alt="">
                <p>{{item.title}}</p>
              </div>
            </template>
            <template v-else="list.data.books">
              <div class="item" v-for="item in list.data.books"  >
                <img :src="item.images.small" alt="">
                <p>{{item.title}}</p>
              </div>
            </template>
          </div>
      </div>
      <div v-if="!flag" style="margin-top: 5rem;display: flex;justify-content: center;align-items: center" >
        <img src="../assets/loading_green.gif" alt="">
      </div>
    </div>
</template>

<script>
    export default {
      props:['data','flag'],
      name: "scroll",
      created(){
      },
      mounted(){
        setTimeout(()=>{
          console.log(this.data)
          console.log(this.flag)
          console.log(this.$refs.content)
        },20);
      }
    }
</script>

<style scoped lang="less">

  .container{
    padding: 0.8rem;
    h3{
      font-size: 1.8rem;
      margin: 1.5rem 0.8rem 1rem 0.8rem;
    }
    .list-box{
      display: flex;
      flex-direction: row;
      max-width: 37.5rem;
      overflow: scroll;
      padding: 0.8rem 0;
      .item{
        margin: 0 0.5rem;
        img{
          width: 14rem;
          height: 18rem;
        }
        p{
          text-align: center;
          font-size: 1.4rem;
          margin: 0.2rem;
        }
      }
    }
  }

  .stars{
    position:relative;
    color:#f5222d;
  }
  .stars:after{
    content:'★★★★★';
    position:absolute;
    top:0;
    left:0;
    width: 4rem;
    overflow: hidden;
    transition: width 2s;
    -moz-transition: width 2s;
    -webkit-transition: width 2s;
    -o-transition: width 2s;
  }
</style>



