<template>
  <!--book 3个展示 样式可以抽离 写一个组件 传属性进去显示-->
  <div>
    <scroll :data="allInfo" :flag="flags"></scroll>

  </div>
</template>

<script>
  import scroll from '../scroll'
  export default {
    name: "movie-view",
    components:{
      scroll
    },
    data(){
      return {
        allInfo:null,
        flags:false
      }
    },
    mounted(){
      this.getApi()
    },
    methods:{
      getApi(){
        this.$axios.all([
          this.$axios.get('https://api.douban.com/v2/book/search?q=虚构类&count=8'),
          this.$axios.get('https://api.douban.com/v2/book/search?q=非虚构类&count=8'),
          this.$axios.get('https://api.douban.com/v2/book/search?q=旅行&count=8')
        ]).then((res)=>{
          this.allInfo=res;
          this.flags =true
        }).catch(err=>{
          console.log(err)
        })
      }
    }
  }
</script>

<style scoped>

</style>



