import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/page3/Home'
import BookView from '@/components/page3/BookView'
import MovieView from '@/components/page3/MovieView'
import StatusView from '@/components/page3/StatusView'
import GroupView from '@/components/page3/GroupView'
import ToastView from '@/components/page3/ToastView'
import store from 'store'

Vue.use(Router)
import { Toast } from 'mint-ui';

export default new Router({
  // history: true,
  // saveScrollPosition: true,
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home,
      children: [
        {
          path: 'BookView',
          name: 'BookView',
          component: BookView
        },
        {
          path: 'MovieView',
          name: 'MovieView',
          component: MovieView
        },
        {
          path: 'StatusView',
          name: 'StatusView',
          component: StatusView
        },
        {
          path: 'GroupView',
          name: 'GroupView',
          component: GroupView
        }
      ]
    },
    {
      path: '/ToastView',
      name: 'ToastView',
      component: ToastView
    },
    {
      path: '*',
      redirect: '/'
    }
  ]
})



