import Vuex from 'vuex'
import Vue from 'vue'


Vue.use(Vuex);

const store=new Vuex.Store({
    state:{
      user:'',
      count:0,
      goodsList:localStorage["goodsList"]?JSON.parse(localStorage["goodsList"]): []
    },
    getters:{  //相当于组件的计算属性，在这里处理好数组返回给组件使用
      sum(state){
        let total = 0;
        for (let i in state.goodsList){
          if(state.goodsList[i].checked){
            total+=state.goodsList[i].price*state.goodsList[i].num
          }
        }
        return total
      }
    },
    mutations:{
      addGoods(state,item){
        for (let i in state.goodsList){
          if(state.goodsList[i].name===item.name){
            state.goodsList[i].num=parseInt(state.goodsList[i].num)+1
            localStorage.setItem("goodsList",JSON.stringify(state.goodsList));
          }
        }
      },
      deleteGoods(state,item){
        console.log(state.goodsList)
        for (let i in state.goodsList){
          if(state.goodsList[i].name===item.name){
            if(parseInt(state.goodsList[i].num)>0){
              state.goodsList[i].num=parseInt(state.goodsList[i].num)-1
              console.log(state.goodsList[i].num)
              localStorage.setItem("goodsList",JSON.stringify(state.goodsList));
            }else {
              return
            }
          }
        }
      },
      change(state,item){
          state.goodsList.forEach((i)=>{
            if(i.name===item.name){
              i.checked=!i.checked
            }
          })
          localStorage.setItem("goodsList",JSON.stringify(state.goodsList));
      },
      getName(state,name){
        state.user=name
      }
    }
  });

export default store





